import {Site} from "./site";
import {model} from "../model";
import {Sidebar} from "./sidebar";

export class App {
    constructor(model) {
        this.model = model
    }

    init() {
        const site = new Site('#site');//—Å–æ–∑–¥–∞–µ–º —Å–∞–π—Ç
        site.render(this.model);//–¥–∞–±–∞–≤–ª—è–µ–º —Ä–µ–Ω–¥–µ—Ä
        const updateCallback = (newBlock) => {//–æ–ø—Ä–µ–¥–µ–ª—è–µ–º —Ñ—É–Ω–∫-—é, –∫–æ—Ç–æ—Ä–∞—è —Å–ª–µ–¥–∏—Ç –∑–∞ –æ–±–Ω–æ–≤–ª–µ–Ω–∏–µ–º
            this.model.push(newBlock);
            site.render(this.model);
        };

        new Sidebar('#panel', updateCallback);
    }
}